import { mapState } from 'vuex'
export default {
  name: 'article-comment',
  props: {
    comments: {
      type: Array,
      require: true
    }
  },
  computed: {
    ...mapState(['user']),
  },
  data() {
    return {
      commentValue: '',
    }
  },
  methods: {
    postComment() {
      this.$emit('postcomment', this.commentValue)
    },
    deleteComment(comment) {
      this.$emit('deletecomment', comment.id)
    },
  },
};
