import { mapState } from 'vuex'
export default {
  name: 'article-meta',
  props: {
    article: {
      type: Object,
      require: true
    }
  },
  computed: {
    ...mapState(['user']),
  },
  methods: {
    clickFavorite() {
      this.$emit('clickfavorite')
    },
    clickFollow() {
      this.$emit('clickfollow')
    },
    clickDelete(slug) {
      this.$emit('clickdelete', slug)
    },
  },
}
